import { __ } from '@wordpress/i18n';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, ToggleControl, __experimentalUnitControl as UnitControl, SelectControl, PanelRow, Dashicon } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import Title from '../../Components/Title';
import IconControl from '../../Components/IconControl';
import ColorsControl from '../../Components/ColorsControl';
import SpaceControl from '../../Components/SpaceControl';
import BorderControl from '../../Components/BorderControl';
import ShadowControl from '../../Components/ShadowControl';
import Typography from '../../Components/Typography';
import { pxUnit, perUnit, emUnit } from '../../Components/Helper/options';

import { types } from './utils/options';

const Settings = ({ attributes, setAttributes }) => {
	const { type, width, alignment, textAlign, icon, isDismiss, typography, colors, padding, border, shadow } = attributes;

	return <>
		<InspectorControls>
			<PanelBody className='bPlPanelBody help' title={__('Help', 'alert-box-block')} initialOpen={false}>
				<div className='helpItem'>
					<a href='https://bblockswp.com/docs/alert-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'alert-box-block')}</a>
				</div>

				<div className='helpItem rateUs'>
					<a href='https://wordpress.org/support/plugin/alert-box-block/reviews/#new-post' target='_blank' rel='noreferrer'>
						<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'alert-box-block')}</span>
						<span>{__('We are new and we need your help to grow!🙏', 'alert-box-block')}</span>
					</a>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Alert Settings', 'alert-box-block')}>
				<PanelRow>
					<Title className=''>{__('Type:', 'alert-box-block')}</Title>
					<SelectControl value={type} onChange={val => {
						setAttributes({ type: val });
						'general' === val && setAttributes({
							width: '100%',
							icon: { class: 'fa fa-exclamation-circle' },
							isDismiss: true,
							colors: { color: '#333', bg: '#fff' },
							padding: { vertical: '15px', horizontal: '30px' },
							border: { ...border, color: '#333', radius: '8px' },
							shadow: {}
						});
						'success' === val && setAttributes({
							width: '100%',
							icon: { class: 'fa fa-check-circle' },
							isDismiss: true,
							colors: { color: '#12b878', bg: '#12b8781a' },
							padding: { vertical: '15px', horizontal: '30px' },
							border: { ...border, color: '#12b878', radius: '8px' },
							shadow: {}
						});
						'info' === val && setAttributes({
							width: '100%',
							icon: { class: 'fa fa-info-circle' },
							isDismiss: true,
							colors: { color: '#2cacff', bg: '#2cacff1a' },
							padding: { vertical: '15px', horizontal: '30px' },
							border: { ...border, color: '#2cacff', radius: '8px' },
							shadow: {}
						});
						'warning' === val && setAttributes({
							width: '100%',
							icon: { class: 'fa fa-cog' },
							isDismiss: true,
							colors: { color: '#f1ae2a', bg: '#f1ae2a1a' },
							padding: { vertical: '15px', horizontal: '30px' },
							border: { ...border, color: '#f1ae2a', radius: '8px' },
							shadow: {}
						});
						'error' === val && setAttributes({
							width: '100%',
							icon: { class: 'fa fa-exclamation-triangle' },
							isDismiss: true,
							colors: { color: '#db4b68', bg: '#db4b681a' },
							padding: { vertical: '15px', horizontal: '30px' },
							border: { ...border, color: '#db4b68', radius: '8px' },
							shadow: {}
						});
						'minimal' === val && setAttributes({
							width: '0px',
							icon: { class: 'fas fa-info-circle' },
							isDismiss: false,
							colors: { color: '#00AB6D', bg: '#fff' },
							padding: { vertical: '10px', horizontal: '10px' },
							border: { ...border, color: '#00AB6D', radius: '50px' },
							shadow: { ...shadow, blur: '10px', color: '#00AB6D' }
						});
					}} options={types} />
				</PanelRow>
				<small>{__('Some settings may change when type will be changed.', 'alert-box-block')}</small>

				<UnitControl className='mt20' label={__('Width:', 'alert-box-block')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit(), perUnit(), emUnit()]} />
				<small>{__('Keep width 0, to auto width.', 'alert-box-block')}</small>

				<IconControl label={__('Alert Icon:', 'alert-box-block')} value={icon} onChange={val => setAttributes({ icon: val })} defaults={{ class: 'fa fa-exclamation-circle' }} isSize={false} isColor={false} />

				<ToggleControl className='mt20' label={__('Alert Dismissible?', 'alert-box-block')} checked={isDismiss} onChange={val => setAttributes({ isDismiss: val })} />

				<Typography className='mt20' label={__('Typography:', 'alert-box-block')} value={typography} onChange={val => setAttributes({ typography: val })} defaults={{ fontSize: { desktop: 25, tablet: 22, mobile: 20 } }} produce={produce} />

				<ColorsControl value={colors} onChange={val => setAttributes({ colors: val })} defaults={{ color: '#333', bg: '#fff' }} />

				<SpaceControl className='mt20' label={__('Padding:', 'alert-box-block')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '15px', horizontal: '30px' }} />

				<BorderControl label={__('Border:', 'alert-box-block')} value={border} onChange={val => setAttributes({ border: val })} defaults={{ width: '2px', color: '#333', radius: '8px' }} />

				<ShadowControl label={__('Shadow:', 'alert-box-block')} value={shadow} onChange={val => setAttributes({ shadow: val })} />
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Alert Box Alignment')} alignmentControls={[
				{ title: __('Alert Box in left', 'alert-box-block'), align: 'left', icon: 'align-left' },
				{ title: __('Alert Box in center', 'alert-box-block'), align: 'center', icon: 'align-center' },
				{ title: __('Alert Box in right', 'alert-box-block'), align: 'right', icon: 'align-right' }
			]} />

			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;